import {ILOGIN, ITEST} from '@/api'
export default {
  name: 'world',
  data () {
    return {
      msg: 'Welcome to Your world'
    }
  },
  methods: {
    fetchTest () {
      this.axios.post(ILOGIN).then((response) => {
        console.dir(response)
      }).catch((err) => {
        console.log(err)
        throw new Error(err)
      })
      this.axios.get(ITEST).then((response) => {
        console.dir(response)
      }).catch((err) => {
        console.log(err)
        throw new Error(err)
      })
    }
  }
}



