

import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import {axioshttp} from '@/widget'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)

Vue.config.productionTip = false

/* eslint-disable no-new */
export const app = new Vue({
el: '#app',
router,
template: '<App/>',
components: { App }
})
app.$vux.toast.show({
text: '请重新登录!',
type: 'text',
width: '50%',
isShowMask: true,
onHide () {
}
})

axioshttp(app)



